 */
.sidebar {
	position: fixed;
	top: 0;
	left: 0;
	overflow-y: scroll;
	box-sizing: border-box;
	width: 240px;
	min-width: 240px;
	height: 100vh;
	min-height: 100vh;
	padding: 10px 0 2rem;
	border-width: 0;
	border-style: solid;
	border-right-width: 3px;
	border-image: linear-gradient(to bottom, #000011, #1164FF) 1/0 3px 0 0/0 stretch;
}

@media only screen and (max-width: 992px) {
	.sidebar {
		position: static;
		width: 100%;
		height: auto;
		min-height: unset;
		margin-bottom: 20px;
		padding-left: 2px;
		transition: all 0.5s ease-out;
		border-bottom-width: 3px;
		border-left-width: 3px;
		border-image: linear-gradient(to bottom, #000011, #1164FF) 1/0 3px 5px/0 stretch;
	}
}

@media only screen and (max-width: 992px) and (prefers-color-scheme: light) {
	.sidebar {
		border-image: linear-gradient(to bottom, #FFFFFF, #1164FF) 1/0 3px 5px/0 stretch;
	}
}

@media (prefers-color-scheme: light) {
	.sidebar {
		border-image: linear-gradient(to bottom, #FFFFFF, #1164FF) 1/0 3px 0 0/0 stretch;
	}
}

@media print {
	.sidebar {
		display: none;
	}
}

.sidebar h2 {
	font-size: 1.5rem;
}

@media only screen and (max-width: 992px) {
	.sidebar > ul {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		padding: 0 5px;
		padding-bottom: 10px;
	}
}

.sidebar > ul > li {
	margin-top: 15px;
}

@media only screen and (max-width: 992px) {
	.sidebar > ul > li {
		margin-top: 10px;
	}
}

@media only screen and (max-width: 768px) {
	.sidebar > ul > li {
		margin-top: 5px;
	}
}

.sidebar > ul > li h3 {
	display: flex;
	align-items: center;
	padding-top: 3px;
	padding-left: 10px;
	animation: flashString 10s linear infinite;
	border-width: 0;
	border-style: solid;
	border-top-width: 3px;
	border-image: linear-gradient(to right, #000011, #1164FF, #FFFF99) 1/3px 0 0/0 stretch;
}

@media only screen and (max-width: 992px) {
	.sidebar > ul > li h3 {
		padding-left: 0;
	}
}

@media (prefers-color-scheme: light) {
	.sidebar > ul > li h3 {
		animation-name: flashStringLight;
		border-image: linear-gradient(to right, #FFFFFF, #1164FF, #FFFF99) 1/3px 0 0/0 stretch;
	}
}

.sidebar > ul > li h3 a {
	display: inline-flex;
	align-items: center;
	margin: auto 0;
	animation: flashString 10s linear infinite;
	line-height: 1em;
}

@media (prefers-color-scheme: light) {
	.sidebar > ul > li h3 a {
		animation-name: flashStringLight;
	}
}

.sidebar > ul > li input[type="checkbox"] {
	display: none;
	visibility: hidden;
}

.sidebar > ul > li input[type="checkbox"].panel-switch:not(:checked) + ul {
	max-height: 0;
	padding: 0;
	border-width: 0;
}

.sidebar > ul > li input[type="checkbox"].panel-switch:not(:checked) + ul li {
	visibility: hidden;
	opacity: 0;
}

.sidebar > ul > li input[type="checkbox"].panel-switch:not(:checked) + ul li a {
	opacity: inherit;
}

.sidebar > ul > li ul {
	margin-left: calc(1em + 12px);
	padding-right: 2px;
	transition: all 0.5s ease-out;
	text-indent: -1em;
}

@media only screen and (max-width: 992px) {
	.sidebar > ul > li ul {
		margin-left: calc(1em + 2px);
	}
}

.sidebar > ul > li ul li {
	transition: all 0.5s ease-out;
}

.sidebar > ul > li ul li:before {
	content: "├";
	padding-right: 2px;
	transition: all 0.5s ease-out;
	animation: flashString 10s linear infinite;
}

@media (prefers-color-scheme: light) {
	.sidebar > ul > li ul li:before {
		animation-name: flashStringLight;
	}
}

.sidebar > ul > li ul li:last-child:before {
	content: "└";
}

.sidebar::-webkit-scrollbar {
	width: 5px;
	height: 5px;
}

@media only screen and (max-width: 992px) {
	.sidebar::-webkit-scrollbar {
		width: 0;
		height: 0;
	}
}

.sidebar::-webkit-scrollbar-track {
	border-radius: 10px;
	background-image: linear-gradient(to bottom, #000011, #1164FF);
}

@media (prefers-color-scheme: light) {
	.sidebar::-webkit-scrollbar-track {
		background-image: linear-gradient(to bottom, #FFFFFF, #1164FF);
	}
}

.sidebar::-webkit-scrollbar-thumb {
	border-radius: 10px;
	background-image: linear-gradient(to bottom right, #CCCCCC, #888888);
}

